.btn-outline-primary:hover {
  color: var(--white);
  background-color: var(--main);
  border-color: var(--main);
}

.btn-close {
  box-sizing: content-box;
  padding: 0;
  margin: 0;
  color: var(--dark);
  border: 0;
  border-radius: 0.25rem;
  opacity: 0.5;
  background-color: transparent;
  font-size: 47px;
  line-height: 1;
}

#content {
  overflow: hidden;
}

.line {
  border-bottom: 3px solid #bbbaba;
  width: 100px;
}

.link-social-media {
  padding-left: 0;
}
.link-social-media > li {
  padding: 5px;
}
@media (min-width: 992px) {
  .link-social-media > li {
    padding: 5px 10px;
  }
}
.link-social-media > li a {
  color: var(--white);
}
.link-social-media > li svg {
  width: 25px;
  height: auto;
}
@media (min-width: 992px) {
  .link-social-media > li svg {
    width: 40px;
  }
}

.grid:after {
  content: "";
  display: block;
  clear: both;
}

.grid-item .news-item-link:hover h2.news-title {
  padding: 10px 10px 5px 10px;
  top: 0;
  bottom: inherit;
}
.grid-item .news-item-link:hover .news-overlay {
  top: 30px;
  left: 30px;
  right: 30px;
  bottom: 30px;
  background-color: var(--main);
  background-image: none;
  opacity: 0.8;
  height: inherit;
}

.grid-sizer,
.grid-item {
  width: 33.333%;
}

@media (max-width: 575px) {
  .grid-sizer,
.grid-item {
    width: 100%;
  }
}
@media (min-width: 576px) and (max-width: 767px) {
  .grid-sizer,
.grid-item {
    width: 50%;
  }
}
