.grid-item {
  float: left;
  padding: 30px;
}

.grid-item img {
  display: block;
  max-width: 100%;
}

.animate {
  transition: opacity 500ms;
  opacity: 0;
}

.animate.animate__animated {
  opacity: 1;
}